import React, { useState, useReducer } from "react";
import { Route, Link, Routes } from "react-router-dom";
import MenuIcon from "@mui/icons-material/Menu";
import { ThemeProvider } from "@mui/material/styles";
import theme from "./theme";
import HomePage from "./project1/HomePage";
import ResetAlerts from "./project1/ResetAlerts";
import {
  Toolbar,
  AppBar,
  Menu,
  MenuItem,
  IconButton,
  Typography,
  Card,
  Snackbar,
  CardHeader,
} from "@mui/material";
import AddAdvisory from "./project1/AddAdvisory";
import LanguageIcon from "@mui/icons-material/Language";
import ListAdvisories from "./project1/ListAdvisories";
const App = () => {
  const [anchorEl, setAnchorEl] = useState(null);

  const msgFromChild = (msg) => {
    setState({ snackBarMsg: msg, contactServer: true });
  };

  const handleClose = () => {
    setAnchorEl(null);
  };
  const handleClick = (event) => {
    setAnchorEl(event.currentTarget);
  };

  const snackbarClose = (event, reason) => {
    if (reason === "clickaway") {
      return;
    }
    setState({ contactServer: false });
  };

  const initialState = {
    snackBarMsg: "",
    contactServer: false,
  };
  const reducer = (state, newState) => ({ ...state, ...newState });
  const [state, setState] = useReducer(reducer, initialState);

  return (
    <ThemeProvider theme={theme}>
      <AppBar position="static">
        <Toolbar>
          <Typography variant="h6" color="inherit">
            INFO3139 - Project1
          </Typography>
          <IconButton
            onClick={handleClick}
            color="inherit"
            style={{ marginLeft: "auto", paddingRight: "1vh" }}
          >
            <MenuIcon />
          </IconButton>
          <Menu
            id="simple-menu"
            anchorEl={anchorEl}
            open={Boolean(anchorEl)}
            onClose={handleClose}
          >
            <MenuItem onClick={handleClose} component={Link} to="/home">
              Home
            </MenuItem>
            <MenuItem onClick={handleClose} component={Link} to="/reset">
              Reset Alerts
            </MenuItem>
            <MenuItem onClick={handleClose} component={Link} to="/add">
              Add Advisory
            </MenuItem>
            <MenuItem onClick={handleClose} component={Link} to="/list">
              List Advisories
            </MenuItem>
          </Menu>
        </Toolbar>
      </AppBar>
      <Card>
        <CardHeader
          title="World Wide Travel Alerts"
          style={{
            display: "flex",
            flexDirection: "column",
            fontSize: "30",
            color: theme.palette.primary.main,
            textAlign: "center",
          }}
          avatar={<LanguageIcon sx={{ fontSize: 100 }} />}
          titleTypographyProps={{ variant: "h5" }}
        />
        <Routes>
          <Route path="/" element={<HomePage />} />
          <Route path="/home" element={<HomePage />} />
          <Route
            path="/reset"
            element={<ResetAlerts dataFromChild={msgFromChild} />}
          />
          <Route
            path="/add"
            element={<AddAdvisory dataFromChild={msgFromChild} />}
          />
          <Route
            path="/list"
            element={<ListAdvisories dataFromChild={msgFromChild} />}
          />
        </Routes>
      </Card>

      <Snackbar
        open={state.contactServer}
        message={state.snackBarMsg}
        autoHideDuration={3000}
        onClose={snackbarClose}
      />
    </ThemeProvider>
  );
};
export default App;
